import React, { Component } from 'react';
import Header from './components/Header/Header';
import PostAddForm from './components/Post-Add-Form/PostAddForm';
import PostFilter from './components/Post-Filter/PostFilter';
import PostList from './components/Post-List/PostList';
import PostSearch from './components/Post-Search/PostSearch';

import style from './App.module.css';

export default class App extends Component {
    constructor(props){
        super(props);
        this.state = {
            data: [
                {label:'React', important: false, like: false,  id:1},
                {label:'Python', important: false, like: false, id:2},
                {label:'Html', important: false, like: false,  id:3}
            ],
            searchText: '',
            filter: 'all'
        };
        this.deletePost = this.deletePost.bind(this);
        this.addPost = this.addPost.bind(this);
        this.onToggleImportant = this.onToggleImportant.bind(this);
        this.onToggleLike = this.onToggleLike.bind(this);
        this.onUpdateSearch = this.onUpdateSearch.bind(this);
        this.onFilter = this.onFilter.bind(this);
        
        this.maxId = 4;

    }

    deletePost(id) {
      
            const items = this.state.data.filter(item => item.id !== id);
            return this.setState({ data: items });
      
    }

    addPost(text){
        const newPost = {
            label: text,
            important: false,
            id: this.maxId++
        }
        this.setState(({data}) => {
            const newArr = [...data, newPost];
            return {
                data: newArr
            }
        });
        
    }

    onToggleImportant(id) {
        this.setState(({data}) => {
            const index = data.findIndex(item => item.id === id);
            
            const oldArr = data[index];
            const newItem = {...oldArr, important: !oldArr.important};

            const newArr = [...data.slice(0, index), newItem, ...data.slice(index + 1)];

            return {
                data: newArr
            }
            
              
        });
    } 

    onToggleLike(id){
        this.setState(({data}) => {
            const index = data.findIndex(item => item.id === id);
            
            const oldArr = data[index];
            const newItem = {...oldArr, like: !oldArr.like};

            const newArr = [...data.slice(0, index), newItem, ...data.slice(index + 1)];

            return {
                data: newArr
            }
            
              
        });
    }

    searchPost(posts, searchText){
        if( searchText.length === 0 ){
            return posts;
        }
        
        return posts.filter(post => {
            const {label, id} = post;
            return label && id 
                ? label.toLowerCase().indexOf(searchText.toLowerCase()) > -1
                : null;
        });
       
    }

    onUpdateSearch(searchText){
        this.setState({searchText});
    }

    filterPost(posts, filter){
        if(filter === 'like'){
            return posts.filter(item => item.like);
        } 
        return posts;
    }

    onFilter(filter){
        this.setState({filter});
    }


    render(){
        const {data, searchText, filter} = this.state;
        const likedPost = data.filter(item => item.like).length;
        const allPosts = data.length;
        const visiblePosts = this.filterPost(this.searchPost(data, searchText), filter);
        return(
            <div className={style.app}>        
                    <Header
                    likedPost={likedPost}
                    allPosts={allPosts}/>
                    <div className="search-panel d-flex">
                        <PostSearch onUpdateSearch={this.onUpdateSearch}/>
                        <PostFilter filter={filter} onFilter={this.onFilter}/>
                    </div>
                    <PostAddForm
                    onAdd={this.addPost}/>    
                    <PostList 
                    posts={visiblePosts}
                    onDelete={this.deletePost}
                    onToggleImportant={this.onToggleImportant}
                    onToggleLike={this.onToggleLike}/>
                         
            </div>
        )
    }
    
};




