import React from 'react';
import './app-header.css';

const Header = ({likedPost, allPosts}) => {
    return (
        <div className="app-header d-flex  align-items-center">
            <h1>Vlad Lisovskiy</h1>
            <h2>Всего {allPosts} записей, из них Вам понравилось: {likedPost}</h2>
        </div>
    )
}

export default Header

