import React, { Component } from 'react';
import './post-status-filter.css';

export default class PostFilter extends Component {
   constructor(props){
       super(props);
       this.buttons = [
           {key: 'all', label: 'Все'},
           {key: 'like', label: 'Понравилось'}

       ];
   }
   
    render(){
    return (
        <div className='btn-group'>
            {this.buttons.map(({key, label}) => {
                const activeClass = this.props.filter === key;
                const additionalClass = activeClass ? 'btn-info' : 'btn-outline-secondary';
                return (
                <button 
                key={key} 
                className={`btn ${additionalClass}`}
                onClick={() => this.props.onFilter(key)}>{label}</button>
                )
            })}
        
            
           
        </div>
    )
   }
}

