import React from 'react';
import './PostListItem.css';


const PostListItem = ({label, onDelete, onToggleImportant, onToggleLike, important, like}) => {
       
        
        let postClassNames = 'app-list-item d-flex justify-content-between';

        if (important){
            postClassNames += ' important';
        }
        if (like){
            postClassNames += ' like';
        }

        return (
            <div className={postClassNames}>
                <span  className="app-list-item-label" onClick={onToggleLike}> {label} </span>       
                <div className="d-flex justify-content-center align-items-center">
                    <button className="btn-star btn-sm" onClick={onToggleImportant}>
                        <i className="fas fa-star"></i>
                    </button>
                    <button className="btn-trash btn-sm" onClick={onDelete}>
                        <i className="fas fa-trash"></i>
                    </button>
                    <i className="fas fa-heart"></i>
                </div>
            </div>
        )
    
}


export default PostListItem;
